import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Toast from './Toast';

const Cart = () => {
  const [cart, setCart] = useState({ items: [], total_amount: 0, total_items: 0 });
  const [loading, setLoading] = useState(true);
  const [updating, setUpdating] = useState(false);
  const [toast, setToast] = useState({ show: false, message: '', type: 'success' });
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const navigate = useNavigate();

  const sessionId = localStorage.getItem('session_id') || 
    (() => {
      const newId = `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
      localStorage.setItem('session_id', newId);
      return newId;
    })();

  useEffect(() => {
    fetchCart();
  }, []);

  const showToast = (message, type = 'success') => {
    setToast({ show: true, message, type });
  };

  const hideToast = () => {
    setToast({ show: false, message: '', type: 'success' });
  };

  const fetchCart = async () => {
    try {
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/cart/${sessionId}`);
      if (response.ok) {
        const cartData = await response.json();
        setCart(cartData);
      }
    } catch (error) {
      console.error('Error fetching cart:', error);
    } finally {
      setLoading(false);
    }
  };

  const updateQuantity = async (productId, size, quantity) => {
    setUpdating(true);
    try {
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/cart/${sessionId}/update`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ product_id: productId, size, quantity })
      });

      if (response.ok) {
        const data = await response.json();
        setCart(data.cart);
        showToast('  cp nht s lng sn phm', 'success');
      } else {
        showToast(' Khng th cp nht s lng sn phm', 'error');
      }
    } catch (error) {
      console.error('Error updating cart:', error);
      showToast(' C li xy ra khi cp nht gi hng', 'error');
    } finally {
      setUpdating(false);
    }
  };

  const removeItem = async (productId, size) => {
    setUpdating(true);
    try {
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/cart/${sessionId}/remove`, {
        method: 'DELETE',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ product_id: productId, size })
      });

      if (response.ok) {
        const data = await response.json();
        setCart(data.cart);
        showToast('  xa sn phm khi gi hng', 'success');
      } else {
        showToast(' Khng th xa sn phm', 'error');
      }
    } catch (error) {
      console.error('Error removing item:', error);
      showToast(' C li xy ra khi xa sn phm', 'error');
    } finally {
      setUpdating(false);
    }
  };

  const clearCart = async () => {
    setShowConfirmModal(true);
  };

  const confirmClearCart = async () => {
    setShowConfirmModal(false);
    setUpdating(true);
    
    try {
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/cart/${sessionId}/clear`, {
        method: 'DELETE'
      });

      if (response.ok) {
        setCart({ items: [], total_amount: 0, total_items: 0 });
        showToast('  xa tt c sn phm khi gi hng!', 'success');
      } else {
        showToast(' Khng th xa gi hng', 'error');
      }
    } catch (error) {
      console.error('Error clearing cart:', error);
      showToast(' C li xy ra khi xa gi hng', 'error');
    } finally {
      setUpdating(false);
    }
  };

  const cancelClearCart = () => {
    setShowConfirmModal(false);
  };

  const formatPrice = (price) => {
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND'
    }).format(price);
  };

  const getDiscountPercentage = (currentPrice, originalPrice) => {
    if (!originalPrice || originalPrice <= currentPrice) return 0;
    return Math.round(((originalPrice - currentPrice) / originalPrice) * 100);
  };

  if (loading) {
    return (
      <div className="cart-page">
        <div className="container">
          <div className="cart-loading">
            <div className="loading-spinner"></div>
            <p>ang ti gi hng...</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="cart-page">
      {/* Cart Content */}
      <div className="cart-content">
        <div className="container">
          <div className="cart-header">
            <h1>
              <ion-icon name="bag-outline"></ion-icon>
              Gi hng ca bn
            </h1>
          </div>

          {cart.items.length === 0 ? (
            <div className="empty-cart">
              <div className="empty-cart-content">
                <ion-icon name="bag-outline"></ion-icon>
                <h3>Gi hng trng</h3>
                <p>Hy thm nhng sn phm trm hng tuyt vi vo gi hng ca bn</p>
                <Link to="/products" className="continue-shopping-btn">
                  <span className="arrow-icon"></span>
                  <span>Tip tc mua sm</span>
                </Link>
              </div>
            </div>
          ) : (
            <div className="cart-layout">
              <div className="cart-items">
                <div className="cart-items-header">
                  <h3>Sn phm  chn</h3>
                  <button 
                    onClick={clearCart} 
                    className="clear-cart-btn"
                    disabled={updating}
                  >
                    <ion-icon name="trash-outline"></ion-icon>
                    Xa tt c
                  </button>
                </div>

                <div className="cart-items-list">
                  {cart.items.map((item, index) => (
                    <div key={`${item.product_id}-${item.size}`} className="cart-item">
                      <div className="cart-item-image">
                        <img src={item.product_image} alt={item.product_name} />
                        {item.original_price && item.original_price > item.size_price && (
                          <div className="cart-item-discount">
                            -{getDiscountPercentage(item.size_price, item.original_price)}%
                          </div>
                        )}
                      </div>

                      <div className="cart-item-info">
                        <h4 className="cart-item-name">{item.product_name}</h4>
                        <div className="cart-item-size">
                          <ion-icon name="resize-outline"></ion-icon>
                          Kch thc: {item.size}
                        </div>
                        <div className="cart-item-price">
                          <span className="current-price">{formatPrice(item.size_price)}</span>
                          {item.original_price && item.original_price > item.size_price && (
                            <span className="original-price">{formatPrice(item.original_price)}</span>
                          )}
                        </div>
                      </div>

                      <div className="cart-item-controls">
                        <div className="quantity-controls">
                          <button 
                            onClick={() => updateQuantity(item.product_id, item.size, item.quantity - 1)}
                            disabled={updating || item.quantity <= 1}
                            className="quantity-btn"
                          >
                            <ion-icon name="remove-outline"></ion-icon>
                          </button>
                          <span className="quantity-display">{item.quantity}</span>
                          <button 
                            onClick={() => updateQuantity(item.product_id, item.size, item.quantity + 1)}
                            disabled={updating}
                            className="quantity-btn"
                          >
                            <ion-icon name="add-outline"></ion-icon>
                          </button>
                        </div>
                        
                        <div className="cart-item-total">
                          {formatPrice(item.total_price)}
                        </div>

                        <button 
                          onClick={() => removeItem(item.product_id, item.size)}
                          disabled={updating}
                          className="remove-item-btn"
                        >
                          <ion-icon name="close-outline"></ion-icon>
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              <div className="cart-sidebar">
                <div className="cart-summary-card">
                  <h3>
                    <ion-icon name="calculator-outline"></ion-icon>
                    Tm tt n hng
                  </h3>
                  
                  <div className="summary-details">
                    <div className="summary-row">
                      <span>Tm tnh ({cart.total_items} sn phm)</span>
                      <span>{formatPrice(cart.total_amount)}</span>
                    </div>
                    <div className="summary-row">
                      <span>Ph vn chuyn</span>
                      <span className="shipping-note">S tnh khi thanh ton</span>
                    </div>
                    <div className="summary-divider"></div>
                    <div className="summary-row total-row">
                      <span>Tng cng</span>
                      <span className="total-amount">{formatPrice(cart.total_amount)}</span>
                    </div>
                  </div>

                  <div className="cart-actions">
                    <button 
                      onClick={() => navigate('/checkout')}
                      className="checkout-btn"
                      disabled={updating}
                    >
                      <ion-icon name="card-outline"></ion-icon>
                      Tin hnh thanh ton
                    </button>
                    
                    <Link to="/products" className="continue-shopping-link">
                      <ion-icon name="arrow-back-outline"></ion-icon>
                      Tip tc mua sm
                    </Link>
                  </div>
                </div>

                <div className="cart-benefits">
                  <h4>
                    <ion-icon name="shield-checkmark-outline"></ion-icon>
                    Li ch khi mua hng
                  </h4>
                  <ul>
                    <li>
                      <ion-icon name="checkmark-circle-outline"></ion-icon>
                      Min ph vn chuyn cho n hng trn 1.000.000
                    </li>
                    <li>
                      <ion-icon name="checkmark-circle-outline"></ion-icon>
                      m bo cht lng 100% t nhin
                    </li>
                    <li>
                      <ion-icon name="checkmark-circle-outline"></ion-icon>
                      H tr i tr trong 7 ngy
                    </li>
                    <li>
                      <ion-icon name="checkmark-circle-outline"></ion-icon>
                      T vn min ph t chuyn gia
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
      
      {/* Confirm Clear Cart Modal */}
      {showConfirmModal && (
        <div className="modal-overlay" onClick={cancelClearCart}>
          <div className="confirm-modal" onClick={(e) => e.stopPropagation()}>
            <div className="confirm-modal-header">
              <ion-icon name="warning-outline"></ion-icon>
              <h3>Xc nhn xa gi hng</h3>
            </div>
            <div className="confirm-modal-body">
              <p>Bn c chc chn mun xa tt c sn phm trong gi hng?</p>
              <p className="confirm-modal-note">Hnh ng ny khng th hon tc!</p>
            </div>
            <div className="confirm-modal-actions">
              <button className="confirm-btn-cancel" onClick={cancelClearCart}>
                <ion-icon name="close-outline"></ion-icon>
                Hy
              </button>
              <button className="confirm-btn-delete" onClick={confirmClearCart} disabled={updating}>
                <ion-icon name="trash-outline"></ion-icon>
                {updating ? 'ang xa...' : 'Xa tt c'}
              </button>
            </div>
          </div>
        </div>
      )}
      
      {/* Toast Notification */}
      <Toast 
        message={toast.message} 
        type={toast.type} 
        isVisible={toast.show} 
        onClose={hideToast}
      />
    </div>
  );
};

export default Cart;