import React, { useState, useEffect } from 'react';
import { useParams, useLocation, Link } from 'react-router-dom';

const OrderConfirmation = () => {
  const { orderId } = useParams();
  const location = useLocation();
  const [order, setOrder] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    // If order data is passed via location state (from checkout)
    if (location.state?.order) {
      setOrder(location.state.order);
      setLoading(false);
    } else {
      // Fetch order data from API
      fetchOrder();
    }
  }, [orderId, location.state]);

  const fetchOrder = async () => {
    try {
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/orders/${orderId}`);
      if (response.ok) {
        const orderData = await response.json();
        setOrder(orderData);
      } else {
        setError('Khng tm thy n hng');
      }
    } catch (error) {
      console.error('Error fetching order:', error);
      setError('C li xy ra khi ti thng tin n hng');
    } finally {
      setLoading(false);
    }
  };

  const formatPrice = (price) => {
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND'
    }).format(price);
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleString('vi-VN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getPaymentMethodText = (method) => {
    return method === 'cod' ? 'Thanh ton khi nhn hng (COD)' : 'Chuyn khon ngn hng';
  };

  const getOrderStatusText = (status) => {
    const statusMap = {
      'pending': 'Ch xc nhn',
      'confirmed': ' xc nhn',
      'processing': 'ang x l',
      'shipping': 'ang giao hng',
      'delivered': ' giao hng',
      'cancelled': ' hy'
    };
    return statusMap[status] || status;
  };

  const getPaymentStatusText = (status) => {
    const statusMap = {
      'pending': 'Ch thanh ton',
      'paid': ' thanh ton',
      'failed': 'Thanh ton tht bi'
    };
    return statusMap[status] || status;
  };

  const getDiscountPercentage = (currentPrice, originalPrice) => {
    if (!originalPrice || originalPrice <= currentPrice) return 0;
    return Math.round(((originalPrice - currentPrice) / originalPrice) * 100);
  };

  if (loading) {
    return (
      <div className="order-confirmation-page">
        <div className="container">
          <div className="order-loading">
            <div className="loading-spinner"></div>
            <p>ang ti thng tin n hng...</p>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="order-confirmation-page">
        <div className="container">
          <div className="order-error">
            <ion-icon name="alert-circle-outline"></ion-icon>
            <h2>C li xy ra</h2>
            <p>{error}</p>
            <Link to="/" className="back-home-btn">
              <ion-icon name="home-outline"></ion-icon>
              V trang ch
            </Link>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="order-confirmation-page">
      {/* Success Banner */}
      <div className="success-banner">
        <div className="container">
          <div className="success-content">
            <div className="success-icon">
              <ion-icon name="checkmark-circle"></ion-icon>
            </div>
            <div className="success-text">
              <h1>t hng thnh cng!</h1>
              <p>Cm n bn  tin tng v t hng ti Khang Trm Hng</p>
            </div>
          </div>
        </div>
      </div>

      <div className="order-details-content">
        <div className="container">
          <div className="order-layout">
            {/* Order Information */}
            <div className="order-main">
              <div className="order-header">
                <div className="order-number">
                  <h2>
                    <ion-icon name="receipt-outline"></ion-icon>
                    n hng #{order.order_number}
                  </h2>
                  <div className="order-meta">
                    <span className="order-date">
                      <ion-icon name="calendar-outline"></ion-icon>
                      Ngy t: {formatDate(order.created_at)}
                    </span>
                    <span className={`order-status status-${order.order_status}`}>
                      <ion-icon name="information-circle-outline"></ion-icon>
                      {getOrderStatusText(order.order_status)}
                    </span>
                  </div>
                </div>
              </div>

              {/* Customer Information */}
              <div className="info-section">
                <h3>
                  <ion-icon name="person-outline"></ion-icon>
                  Thng tin khch hng
                </h3>
                <div className="info-grid">
                  <div className="info-item">
                    <label>H tn:</label>
                    <span>{order.customer_info.full_name}</span>
                  </div>
                  <div className="info-item">
                    <label>S in thoi:</label>
                    <span>{order.customer_info.phone}</span>
                  </div>
                  <div className="info-item">
                    <label>Email:</label>
                    <span>{order.customer_info.email}</span>
                  </div>
                  <div className="info-item full-width">
                    <label>a ch giao hng:</label>
                    <span>
                      {order.customer_info.address}, {order.customer_info.ward}, {order.customer_info.district}, {order.customer_info.city}
                    </span>
                  </div>
                  {order.customer_info.notes && (
                    <div className="info-item full-width">
                      <label>Ghi ch:</label>
                      <span>{order.customer_info.notes}</span>
                    </div>
                  )}
                </div>
              </div>

              {/* Order Items */}
              <div className="info-section">
                <h3>
                  <ion-icon name="bag-outline"></ion-icon>
                  Sn phm  t
                </h3>
                <div className="order-items-list">
                  {order.items.map((item, index) => (
                    <div key={index} className="order-item">
                      <div className="order-item-image">
                        <img src={item.product_image} alt={item.product_name} />
                        <span className="order-item-quantity">{item.quantity}</span>
                        {item.original_price && item.original_price > item.size_price && (
                          <div className="order-item-discount">
                            -{getDiscountPercentage(item.size_price, item.original_price)}%
                          </div>
                        )}
                      </div>
                      
                      <div className="order-item-details">
                        <h4>{item.product_name}</h4>
                        <div className="order-item-specs">
                          <span className="item-size">
                            <ion-icon name="resize-outline"></ion-icon>
                            Kch thc: {item.size}
                          </span>
                          <span className="item-price">
                            <span className="current-price">{formatPrice(item.size_price)}</span>
                            {item.original_price && item.original_price > item.size_price && (
                              <span className="original-price">{formatPrice(item.original_price)}</span>
                            )}
                          </span>
                        </div>
                        <div className="item-total">
                          Thnh tin: <strong>{formatPrice(item.total_price)}</strong>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              {/* Payment Information */}
              <div className="info-section">
                <h3>
                  <ion-icon name="card-outline"></ion-icon>
                  Thng tin thanh ton
                </h3>
                <div className="payment-info">
                  <div className="payment-method">
                    <label>Phng thc thanh ton:</label>
                    <span>
                      {order.payment_method === 'cod' ? (
                        <>
                          <ion-icon name="cash-outline"></ion-icon>
                          {getPaymentMethodText(order.payment_method)}
                        </>
                      ) : (
                        <>
                          <ion-icon name="card-outline"></ion-icon>
                          {getPaymentMethodText(order.payment_method)}
                        </>
                      )}
                    </span>
                  </div>
                  <div className="payment-status">
                    <label>Trng thi thanh ton:</label>
                    <span className={`status-${order.payment_status}`}>
                      {getPaymentStatusText(order.payment_status)}
                    </span>
                  </div>
                </div>

                {order.payment_method === 'bank_transfer' && order.payment_status === 'pending' && (
                  <div className="bank-transfer-info">
                    <h4>
                      <ion-icon name="information-circle-outline"></ion-icon>
                      Thng tin chuyn khon
                    </h4>
                    <div className="bank-details">
                      <div className="bank-item">
                        <strong>Ngn hng:</strong> Vietcombank
                      </div>
                      <div className="bank-item">
                        <strong>S ti khon:</strong> 1234567890
                      </div>
                      <div className="bank-item">
                        <strong>Ch ti khon:</strong> KHANG TRAM HUONG
                      </div>
                      <div className="bank-item highlight">
                        <strong>Ni dung chuyn khon:</strong> Thanh toan don hang {order.order_number}
                      </div>
                      <div className="bank-item highlight">
                        <strong>S tin:</strong> {formatPrice(order.total_amount)}
                      </div>
                    </div>
                    <p className="bank-note">
                      <ion-icon name="information-circle-outline"></ion-icon>
                      Vui lng chuyn khon ng s tin v ni dung  n hng c x l nhanh chng.
                    </p>
                  </div>
                )}
              </div>
            </div>

            {/* Order Summary Sidebar */}
            <div className="order-sidebar">
              <div className="order-summary-card">
                <h3>
                  <ion-icon name="calculator-outline"></ion-icon>
                  Tm tt n hng
                </h3>
                
                <div className="summary-calculations">
                  <div className="calc-row">
                    <span>Tm tnh ({order.items.reduce((sum, item) => sum + item.quantity, 0)} sn phm)</span>
                    <span>{formatPrice(order.subtotal)}</span>
                  </div>
                  <div className="calc-row">
                    <span>Ph vn chuyn</span>
                    <span>{formatPrice(order.shipping_fee)}</span>
                  </div>
                  {order.discount > 0 && (
                    <div className="calc-row discount-row">
                      <span>Gim gi</span>
                      <span>-{formatPrice(order.discount)}</span>
                    </div>
                  )}
                  <div className="calc-divider"></div>
                  <div className="calc-row total-row">
                    <span>Tng cng</span>
                    <span className="total-amount">{formatPrice(order.total_amount)}</span>
                  </div>
                </div>
              </div>

              <div className="order-actions">
                <Link to="/products" className="continue-shopping-btn">
                  <ion-icon name="arrow-back-outline"></ion-icon>
                  Tip tc mua sm
                </Link>
                <Link to="/" className="back-home-btn">
                  <ion-icon name="home-outline"></ion-icon>
                  V trang ch
                </Link>
              </div>

              <div className="order-support">
                <h4>
                  <ion-icon name="headset-outline"></ion-icon>
                  Cn h tr?
                </h4>
                <div className="support-contacts">
                  <a href="tel:0123456789" className="support-item">
                    <ion-icon name="call-outline"></ion-icon>
                    <div>
                      <strong>Hotline</strong>
                      <span>0123 456 789</span>
                    </div>
                  </a>
                  <a href="mailto:support@khangtramhuong.com" className="support-item">
                    <ion-icon name="mail-outline"></ion-icon>
                    <div>
                      <strong>Email</strong>
                      <span>support@khangtramhuong.com</span>
                    </div>
                  </a>
                </div>
              </div>

              <div className="order-next-steps">
                <h4>
                  <ion-icon name="list-outline"></ion-icon>
                  Cc bc tip theo
                </h4>
                <ul>
                  <li>
                    <ion-icon name="checkmark-circle-outline"></ion-icon>
                    Chng ti s xc nhn n hng trong 2-4 gi
                  </li>
                  <li>
                    <ion-icon name="cube-outline"></ion-icon>
                    ng gi v chun b hng trong 1-2 ngy
                  </li>
                  <li>
                    <ion-icon name="car-outline"></ion-icon>
                    Giao hng trong 3-7 ngy lm vic
                  </li>
                  <li>
                    <ion-icon name="star-outline"></ion-icon>
                    nh gi sn phm sau khi nhn hng
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OrderConfirmation;