import React, { useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";
import axios from "axios";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const ProductsPage = () => {
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [sortBy, setSortBy] = useState('newest');
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [searchExpanded, setSearchExpanded] = useState(false);

  // Categories
  const categories = [
    { id: 'all', name: 'Tt C', icon: '' },
    { id: 'ky-nam', name: 'K Nam', icon: '' },
    { id: 'tu-nhien', name: 'T Nhin', icon: '' },
    { id: 'truyen-thong', name: 'Truyn Thng', icon: '' },
    { id: 'sang', name: 'Sng', icon: '' }
  ];
  const seoData = {
    title: "Trm Hng Cao Cp - Sn Phm Cht Lng | Khang Trm Hng",
    description: "Khm ph cc sn phm trm hng cao cp 100% t nhin. Cht lng vt tri vi gi tt nht. Giao hng ton quc, cht lng m bo.",
    url: "https://khangtramhuong.vn/san-pham",
    image: "https://images.pexels.com/photos/3639806/pexels-photo-3639806.jpeg",
    keywords: "trm hng, k nam, trm hng cao cp, trm hng t nhin, mua trm hng, trm hng vit nam"
  };

  // Customer Reviews Data
  const customerReviews = [
    {
      id: 1,
      name: "Nguyn Vn An",
      rating: 5,
      comment: "Trm hng cht lng tuyt vi! Hng thm rt m  v t nhin. S quay li mua tip.",
      date: "2 ngy trc",
      product: "Trm Hng K Nam",
      verified: true
    },
    {
      id: 2,
      name: "Trn Th Bnh",
      rating: 5,
      comment: "Mnh  s dng nhiu loi trm hng nhng ca shop ny thc s khc bit. Hng thm thanh thot, rt thch hp cho thin nh.",
      date: "5 ngy trc",
      product: "Trm Hng Sng",
      verified: true
    },
    {
      id: 3,
      name: "L Minh Cng",
      rating: 4,
      comment: "Sn phm tt, ng gi cn thn. Hng thm t nhin, khng b pha trn. Gi c hp l.",
      date: "1 tun trc",
      product: "Trm Hng T Nhin",
      verified: true
    },
    {
      id: 4,
      name: "Phm Th Dung",
      rating: 5,
      comment: "Trm hng truyn thng rt cht lng! Mnh mua lm qu tng v mi ngi u khen ngi.",
      date: "1 tun trc",
      product: "Trm Hng Truyn Thng",
      verified: true
    },
    {
      id: 5,
      name: "Hong Vn c",
      rating: 5,
      comment: "Shop t vn rt nhit tnh, sn phm ng nh m t. Hng thm nng nn, rt ng tin!",
      date: "2 tun trc",
      product: "Trm Hng K Nam",
      verified: true
    },
    {
      id: 6,
      name: "V Th Hoa",
      rating: 4,
      comment: "Ln u mua trm hng online, ban u hi lo lng nhng sn phm nhn v rt ok. S ng h shop lu di.",
      date: "2 tun trc",
      product: "Trm Hng Sng",
      verified: false
    }
  ];

  // Fetch products
  useEffect(() => {
    const fetchProducts = async () => {
      try {
        setLoading(true);
        setError(null);
        
        if (!BACKEND_URL) {
          throw new Error('Backend URL not configured');
        }
        
        const response = await axios.get(`${API}/products`, {
          timeout: 10000,
          headers: {
            'Content-Type': 'application/json'
          }
        });
        
        if (response.data && Array.isArray(response.data)) {
          setProducts(response.data);
          setFilteredProducts(response.data);
        } else {
          setProducts([]);
          setFilteredProducts([]);
        }
      } catch (error) {
        console.error('Error fetching products:', error);
        setError('Khng th ti sn phm. Vui lng th li sau.');
        setProducts([]);
        setFilteredProducts([]);
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, [API, BACKEND_URL]);

  // Filter and sort products
  useEffect(() => {
    if (!products || products.length === 0) {
      setFilteredProducts([]);
      return;
    }

    let filtered = [...products];

    // Filter by category
    if (selectedCategory !== 'all') {
      filtered = filtered.filter(product => {
        if (!product) return false;
        const category = product.category || '';
        const name = product.name || '';
        const searchTerm = selectedCategory.replace('-', ' ').toLowerCase();
        
        return category.toLowerCase().includes(searchTerm) ||
               name.toLowerCase().includes(searchTerm);
      });
    }

    // Filter by search
    if (searchQuery && searchQuery.trim()) {
      const query = searchQuery.toLowerCase().trim();
      filtered = filtered.filter(product => {
        if (!product) return false;
        const name = product.name || '';
        const description = product.description || '';
        
        return name.toLowerCase().includes(query) ||
               description.toLowerCase().includes(query);
      });
    }

    // Sort products
    switch (sortBy) {
      case 'price-low':
        filtered.sort((a, b) => (a.price || 0) - (b.price || 0));
        break;
      case 'price-high':
        filtered.sort((a, b) => (b.price || 0) - (a.price || 0));
        break;
      case 'name':
        filtered.sort((a, b) => (a.name || '').localeCompare(b.name || ''));
        break;
      default:
        // Keep original order (newest)
        break;
    }

    setFilteredProducts(filtered);
  }, [products, selectedCategory, sortBy, searchQuery]);

  // Format price safely
  const formatPrice = (price) => {
    if (!price || isNaN(price)) return '0 VN';
    try {
      return new Intl.NumberFormat('vi-VN').format(price) + ' VN';
    } catch (error) {
      return price + ' VN';
    }
  };

  // Render loading state
  if (loading) {
    return (
      <div className="compact-products-page">
        <div className="compact-loading">
          <div className="compact-spinner"></div>
          <p>ang ti...</p>
        </div>
      </div>
    );
  }

  // Render error state
  if (error) {
    return (
      <div className="compact-products-page" style={{ marginTop: '80px' }}>
        <div className="container">
          <div className="error-state">
            <ion-icon name="alert-circle-outline"></ion-icon>
            <h3>C li xy ra</h3>
            <p>{error}</p>
            <button 
              onClick={() => window.location.reload()} 
              className="retry-btn"
            >
              Th li
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="compact-products-page" style={{ marginTop: '80px' }}>
      <Helmet>
        <title>{seoData.title}</title>
        <meta name="description" content={seoData.description} />
        <meta name="keywords" content={seoData.keywords} />
        <meta property="og:title" content={seoData.title} />
        <meta property="og:description" content={seoData.description} />
        <meta property="og:image" content={seoData.image} />
        <meta property="og:url" content={seoData.url} />
        <meta name="twitter:card" content="summary_large_image" />
        <link rel="canonical" href={seoData.url} />
      </Helmet>

      <div className="container">
        {/* Compact Action Bar */}
        <div className="action-bar">
          <div className="action-left">
            {/* Category Dropdown */}
            <div className="category-dropdown">
              <select
                value={selectedCategory}
                onChange={(e) => setSelectedCategory(e.target.value)}
                className="category-select"
              >
                {categories.map(category => (
                  <option key={category.id} value={category.id}>
                    {category.icon} {category.name}
                  </option>
                ))}
              </select>
            </div>
          </div>

          <div className="action-right">
            {/* Search Toggle with Icon */}
            <div className={`search-container ${searchExpanded ? 'expanded' : ''}`}>
              <button
                className="search-toggle"
                onClick={() => setSearchExpanded(!searchExpanded)}
                aria-label="Tm kim"
              >
                <ion-icon name="search-outline"></ion-icon>
              </button>
              <div className={`search-input-wrapper ${searchExpanded ? 'show' : ''}`}>
                <input
                  type="text"
                  placeholder="Tm kim sn phm..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="search-input"
                  autoFocus={searchExpanded}
                />
                {searchQuery && (
                  <button
                    className="search-clear"
                    onClick={() => setSearchQuery('')}
                    aria-label="Xa tm kim"
                  >
                    <ion-icon name="close-outline"></ion-icon>
                  </button>
                )}
              </div>
            </div>

            {/* Sort Dropdown */}
            <select
              className="sort-select"
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
            >
              <option value="newest">Mi nht</option>
              <option value="price-low">Gi thp</option>
              <option value="price-high">Gi cao</option>
              <option value="name">Tn A-Z</option>
            </select>
          </div>
        </div>

        {/* Products Section */}
        <div className="products-section">
          <div className="section-info">
            <h2>{selectedCategory === 'all' ? 'Tt C Sn Phm' : categories.find(c => c.id === selectedCategory)?.name}</h2>
            <span className="count">{filteredProducts.length} sn phm</span>
          </div>

          {filteredProducts.length === 0 ? (
            <div className="no-results">
              <ion-icon name="search-outline"></ion-icon>
              <h3>Khng tm thy sn phm</h3>
              <p>Th t kha khc hoc chn danh mc khc</p>
            </div>
          ) : (
            <div className="compact-grid">
              {filteredProducts.map((product, index) => {
                if (!product || !product.id) return null;
                
                return (
                  <div key={`${product.id}-${index}`} className="compact-card">
                    <div className="card-image">
                      <img 
                        src={product.image_url || product.image || 'https://images.pexels.com/photos/3639806/pexels-photo-3639806.jpeg'} 
                        alt={product.name || 'Sn phm'} 
                        onError={(e) => {
                          e.target.src = 'https://images.pexels.com/photos/3639806/pexels-photo-3639806.jpeg';
                        }}
                        loading="lazy"
                      />
                      <div className="image-overlay">
                        <Link 
                          to={`/product/${product.id}`} 
                          className="quick-view"
                          aria-label={`Xem chi tit ${product.name}`}
                        >
                          <ion-icon name="eye-outline"></ion-icon>
                        </Link>
                      </div>
                      {product.featured && (
                        <div className="featured-badge">
                          <ion-icon name="star"></ion-icon>
                        </div>
                      )}
                    </div>
                    <div className="card-content">
                      <h3 className="product-title">{product.name || 'Tn sn phm'}</h3>
                      <p className="product-desc">{product.description || 'M t sn phm'}</p>
                      <div className="product-bottom">
                        <div className="price-info">
                          <span className="current-price">{formatPrice(product.price)}</span>
                          {product.original_price && product.original_price > product.price && (
                            <span className="old-price">{formatPrice(product.original_price)}</span>
                          )}
                        </div>
                        <div className="rating">
                          {[...Array(5)].map((_, i) => (
                            <ion-icon
                              key={i}
                              name={i < (product.rating || 0) ? "star" : "star-outline"}
                              className={i < (product.rating || 0) ? "filled" : ""}
                            ></ion-icon>
                          ))}
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </div>

        {/* Customer Reviews Section */}
        <div className="reviews-section">
          <div className="reviews-header">
            <h2 className="reviews-title">
              <ion-icon name="chatbubbles-outline"></ion-icon>
              nh Gi Khch Hng
            </h2>
            <div className="reviews-stats">
              <div className="average-rating">
                <span className="rating-number">4.8</span>
                <div className="stars">
                  {[...Array(5)].map((_, i) => (
                    <ion-icon key={i} name="star" className="filled"></ion-icon>
                  ))}
                </div>
                <span className="reviews-count">({customerReviews.length} nh gi)</span>
              </div>
            </div>
          </div>

          <div className="reviews-grid">
            {customerReviews.map(review => (
              <div key={review.id} className="review-card">
                <div className="review-header">
                  <div className="reviewer-info">
                    <div className="reviewer-details">
                      <h4 className="reviewer-name">
                        {review.name}
                        {review.verified && (
                          <span className="verified-badge">
                            <ion-icon name="checkmark-circle"></ion-icon>
                          </span>
                        )}
                      </h4>
                      <div className="review-meta">
                        <div className="review-rating">
                          {[...Array(5)].map((_, i) => (
                            <ion-icon
                              key={i}
                              name={i < review.rating ? "star" : "star-outline"}
                              className={i < review.rating ? "filled" : ""}
                            ></ion-icon>
                          ))}
                        </div>
                        <span className="review-date">{review.date}</span>
                      </div>
                    </div>
                  </div>
                  <div className="review-product">
                    <span className="product-tag">{review.product}</span>
                  </div>
                </div>
                <div className="review-content">
                  <p className="review-comment">{review.comment}</p>
                </div>
              </div>
            ))}
          </div>

          <div className="reviews-footer">
            <button className="load-more-reviews">
              <ion-icon name="refresh-outline"></ion-icon>
              Xem thm nh gi
            </button>
            <div className="trust-indicators">
              <div className="trust-item">
                <ion-icon name="shield-checkmark-outline"></ion-icon>
                <span>100% nh gi tht</span>
              </div>
              <div className="trust-item">
                <ion-icon name="people-outline"></ion-icon>
                <span>1,200+ khch hng tin tng</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductsPage;